<template>
  <div class="reply-bar-container">
    <div class="reply-bar">
      <vs-avatar class="avatar" @click.native="handleAvatarClick" :src="avatar" v-if="avatar"></vs-avatar>
      <div class="reply-bar-input" @click="handleClick">
        ...
      </div>
    </div>
  </div>
</template>

<script>
import {Avatar} from "../../lib";

export default {
  name: "vs-reply-bar",
  components: {
    'vs-avatar': Avatar
  },
  props: {
    avatar: {
      type: String,
      default: ''
    }
  },
  setup(props, context) {
    const handleAvatarClick = () => {
      context.emit('avatar')
    }
    const handleClick = () => {
      context.emit('click')
    }
    return {
      handleAvatarClick,
      handleClick
    }
  }
}
</script>

